___CSS_LOADER_EXPORT___.push([module.id, `* {
  padding: 0px;
  margin: 0px;
  font-family: Arial, Helvetica, sans-serif;
}

body {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background-image: linear-gradient(to right, #0011ff, #00e1ff);
    color: white;
}

header {
    width: 100%;
    padding: 12px;
    display: flex;
    justify-content: flex-end;
}

.search-box {
    margin: 2px 14px;
}

.search-box-input {
    border: none;
    outline: none;
    padding: 4px 8px;
    border-radius: 12px;
    background: rgba(128, 128, 128, 0.362);
    color: white;
    font-size: 1.2rem;
}

.search-box-input::placeholder {
    color: white;
    font-size: 1.2rem;
}


.content {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.title {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    font-size: 3rem;
}

.weather {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    gap: 32px;
}

.temp {
    font-size: 8rem;
    font-weight: bold;
}

.divider {  
    border: 2px solid white;
    height: 100px;
}

.extras {
    font-size: 1.5rem;
    font-weight: bold;
